import { request } from 'utils';

const prefix = '/api/categories';

export const getComeFrom = () =>
  request({
    prefix,
    url: '/ComeFrom',
    method: 'GET',
    params: {},
    data: {},
  });
export const getComeFromById = id =>
  request({
    prefix,
    url: `/ComeFrom/${  id}`,
    method: 'GET',
    params: {},
    data: {},
  });
export const createComeFrom = data =>
  request({
    prefix,
    url: '/ComeFrom',
    method: 'POST',
    params: {},
    data,
  });
export const updateComeFrom = (id, data) =>
  request({
    prefix,
    url: `/ComeFrom/${  id}`,
    method: 'PUT',
    params: {},
    data,
  });
export const deleteComeFrom = id =>
  request({
    prefix,
    url: `/ComeFrom/${  id}`,
    method: 'DELETE',
    params: {},
    data: {},
  });



