import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';

import { Row, Card, Form, Input, message } from 'antd';

import Page from 'components/LayoutComponents/Page';
import BtnSave from 'components/common/button/BtnSave';
import BtnCancel from 'components/common/button/BtnCancel';
import { getComeFromById, updateComeFrom, createComeFrom } from 'services/comeFrom';

const TIME_RETURN = 1500;

@Form.create({ name: 'formSpcializeTraining' })
class FormComeFrom extends Component {
  constructor(props) {
    super(props);
    this.state = {
      id: null,
      initialValue: {},
    };
    this.updateTitle ='Cập nhật xuất thân';
    this.addNewTitle = 'Thêm xuất thân';
  }

  componentDidMount = async () => {
    const { match } = this.props;
    if (match.params.id) {
      const ComeFromByID = await getComeFromById(match.params.id);
      console.log('ComeFromByID',ComeFromByID);
      const initialValue = this.handleInitialValue(ComeFromByID.data);
      console.log('initialValue',initialValue);
      this.setState({ id: match.params.id, initialValue });
    }
  };

  handleInitialValue = initialValue => {
    const initialValueDefault = {
      id: '',
      label: '',
      labelInEngLish: '',
    };
    return { ...initialValueDefault, ...initialValue };
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        console.log('Received values of form: ', values);
        // edit
        if (this.state.id) {
          values.id = this.state.id;
          updateComeFrom(values.id,values).then( res => {
            console.log('res update',res);
            if (res.status === 200) {
              message.success('Cập nhật xuất thân thành công.');
              setTimeout(() => this.props.history.goBack(), TIME_RETURN);
            } else {
              message.error(res.data.message);
            }
          });
        } else {
          createComeFrom(values).then( res => {
            console.log('res create',res);
            if (res.status === 200) {
              message.success('Tạo xuất thân thành công.');
              setTimeout(() => this.props.history.goBack(), TIME_RETURN);
            } else {
              message.error(res.data.message);
            }
          });
        }
      } else {
        console.log('err form',err);
      }
    });
  };

  render() {
    const { initialValue } = this.state;
    const { getFieldDecorator } = this.props.form;
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 5 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 },
      },
      colon: false,
    };
    const { id: specializeTrainingId } = this.state;
    return (
      <Card title={specializeTrainingId ? this.updateTitle : this.addNewTitle}>
        <Row>
          <Form {...formItemLayout} onSubmit={this.handleSubmit} className="login-form">
            <Form.Item {...formItemLayout} label="Tên xuất thân" hasFeedback>
              {getFieldDecorator('label', {
                initialValue: initialValue.label,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<Input />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Tên tiếng anh" hasFeedback>
              {getFieldDecorator('labelInEnglish', {
                initialValue: initialValue.labelInEnglish,
              })(<Input />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label=" ">
              <BtnSave htmlType="submit" />
              <BtnCancel onClick={() => this.props.history.goBack()} />
            </Form.Item>
          </Form>
        </Row>
      </Card>
    );
  }
}

export default withRouter(props => (
  <Page {...props}>
    <FormComeFrom {...props} />
  </Page>
));



